@charset "UTF-8";
body {
  font: 14px/2 "Helvetica Neue",Helvetica,Arial,"Microsoft Yahei","Hiragino Sans GB","Heiti SC","WenQuanYi Micro Hei",sans-serif;
  color: #333;
}

a {
  text-decoration: none;
  color: #333;
}

ul, li, ol {
  margin: 0;
  padding: 0;
  list-style: none;
}

button, figure {
  margin: 0;
  padding: 0;
}

input[type="button"], input[type="submit"], input[type="reset"] {
  -webkit-appearance: none;
}

img {
  vertical-align: middle;
}

.img-responsive {
  @include img-responsive;
}

.img-circle {
  border-radius: 50%;
}

// 1px线
%border-x{
  background-repeat: repeat-x;
  -webkit-background-size: 100% 1px;
  background-image: -webkit-gradient(linear,left top,left bottom,color-stop(0.5,transparent),color-stop(0.5,#e0e0e0),to(#e0e0e0));
}
%border-y{
  background-repeat: repeat-y;
  -webkit-background-size: 1px 100%;
  background-image: -webkit-gradient(linear,left top,right top,color-stop(0.5,transparent),color-stop(0.5,#e0e0e0),to(#e0e0e0));
}

%border-all{
  position: relative;
  &:after{
    content: "";
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background-image: -webkit-gradient(linear,left bottom,left top,color-stop(0.5,transparent),color-stop(0.5,#e0e0e0),to(#e0e0e0)),-webkit-gradient(linear,left top,right top,color-stop(0.5,transparent),color-stop(0.5,#e0e0e0),to(#e0e0e0)),-webkit-gradient(linear,left top,left bottom,color-stop(0.5,transparent),color-stop(0.5,#e0e0e0),to(#e0e0e0)),-webkit-gradient(linear,right top,left top,color-stop(0.5,transparent),color-stop(0.5,#e0e0e0),to(#e0e0e0));
    -webkit-background-size: 100% 1px,1px 100%,100% 1px,1px 100%;
    background-size: 100% 1px,1px 100%,100% 1px,1px 100%;
    background-repeat: no-repeat;
    background-position: top,right,bottom,left;
    padding: 1px;
    -webkit-box-sizing: border-box;
    z-index: 10;
    pointer-events: none;
}

}
%border-radius{
  position: relative;
  &:before {
    content: "";
    width: 200%;
    height: 200%;
    position: absolute;
    top: 0;
    left: 0;
    border: 1px solid #e0e0e0;
    -webkit-transform: scale(0.5);
    -webkit-transform-origin: 0 0;
    padding: 1px;
    -webkit-box-sizing: border-box;
    border-radius: 4px;
    pointer-events: none;
}
}
%border-top {
  @extend %border-x;
  background-position: left top;
}
%border-right {
  @extend %border-y;
  background-position: right top;
}
%border-bottom {
  @extend %border-x;
  background-position: left bottom;
}
%border-left {
  @extend %border-y;
  background-position: left top;
}


// 加载更多
.loading{
  width: 100%;
  margin-bottom: 10px;
  line-height: 50px;
  text-align: center;
}

// 底部置顶
.fixed-bottom {
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  .btn {
    border-radius: 0;
  }
}

.fixed-placeholder {
  padding-bottom: 65px;
}

// 对齐
.text-left {
  text-align: left;
}

.text-right {
  text-align: right;
}

.text-center {
  text-align: center;
}

.text-justify {
  text-align: justify;
}

.text-nowrap {
  white-space: nowrap;
}

