@charset "UTF-8";
// 顶部
.index-head-wrap {
  border-bottom: 2px solid $brand-primary;
  margin-bottom: 15px;
  background-color: #fff;
  .link{
    a {
      display: inline-block;
      margin-left: 30px;
      font-size: 12px;
      line-height: 30px;
    }
  }
  .logo {
    float: left;
    width: 200px;
    margin-top: 15px;
  }
  .search {
    position: relative;
    float: right;
    width: 300px;
    margin-top: 10px;
    input {
      width: 300px;
      height: 32px;
      padding-left: 10px;
      line-height: 30px;
      border: 1px solid $brand-primary;
      border-radius: 17px;
    }
  }
  .btn-submit {
    position: absolute;
    top: 6px;
    right: 0;
    display: block;
    width: 34px;
    height: 34px;
    background: url("../img/icon_search.png") no-repeat 0 0;
    border: none;
  }
}

// 侧边栏
.aside-bar{
  position: fixed;
  top: 120px;
  width: 100px;
  padding: 15px;
  background: #fff;
  box-shadow: 0 0 4px rgba(0,0,0,.3);
  border-top-right-radius: 8px;
  border-bottom-right-radius: 8px;
  a{
    display: block;
    text-align: center;
    padding-top: 10px;
    padding-bottom: 10px;
    &:hover,&.active{
      .icon-home{
        background-image: url("../img/icon_home2.png");
      }
      .icon-work{
        background-image: url("../img/icon_work2.png");
      }
      .icon-xiezuo{
        background-image: url("../img/icon_xiezuo2.png");
      }
      .icon-wenhua{
        background-image: url("../img/icon_wenhua2.png");
      }
      .icon-system{
        background-image: url("../img/icon_system2.png");
      }
    }
  }
  .icon{
    display: inline-block;
    width: 45px;
    height: 45px;
    background-position: center center;
    background-repeat: no-repeat;
    text-align: center;
  }
  .icon-home{
    background-image: url("../img/icon_home1.png");
  }
  .icon-work{
    background-image: url("../img/icon_work1.png");
  }
  .icon-xiezuo{
    background-image: url("../img/icon_xiezuo1.png");
  }
  .icon-wenhua{
    background-image: url("../img/icon_wenhua1.png");
  }
  .icon-system{
    background-image: url("../img/icon_system1.png");
  }
}

// 内容区
.main-wrap {
  width: 1000px;
  @include clearfix();
  margin: 0 auto 15px;
  .left-wrap,
  .middle-wrap,
  .right-wrap {
    float: left;
  }
  .left-wrap {
    width: 220px;
  }
  .middle-wrap {
    width: 480px;
    margin-left: 15px;
  }
  .right-wrap {
    width: 270px;
    margin-left: 15px;
  }
}

.mod-hd {
  height: 34px;
  line-height: 34px;
  padding-left: 20px;
  //margin-bottom: 10px;
  //border-bottom: 2px solid #ddd;
  color: #fff;
  background-color: $brand-primary;
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
  h2 {
    display: inline-block;
    margin: 0;
    font-size: 14px;
    font-weight: normal;
    //border-bottom: 2px solid $brand-primary;
  }
  .more {
    float: right;
    margin-right: 20px;
    color: #fff;
    font-size: 12px;
  }
  li {
    display: inline-block;
    line-height: 40px;
    padding-left: 10px;
    padding-right: 10px;
    cursor: pointer;
    &.active {
      line-height: 30px;
      padding-bottom: 10px;
      background-color: #fff;
      color: $gray-darker;
      border-top-left-radius: 4px;
      border-top-right-radius: 4px;
    }
  }
}

.mod-bd {
  background-color: #fff;
  border-bottom-left-radius: 8px;
  border-bottom-right-radius: 8px;
}

// 会员中心
.mem-center {
  height: 166px;
  color: #fff;
  background-color: $brand-primary;
  border-radius: 8px;
  .mem-info {
    line-height: 30px;
    margin-right: 20px;
    padding: 15px 0 0 20px;
    //padding-top: 15px;
    //padding-left: 50px;
    font-size: 16px;
    //background: url("../img/avatar.png") no-repeat left center;
    border-bottom: 2px solid rgba(255, 255, 255, .8);
    span{
      margin-left: 10px;
      strong{
        font-weight: normal;
      }
    }
  }
  dl {
    margin: 0;
  }
  dt, dd {
    padding-left: 20px;
    padding-right: 20px;
    margin-left: 0;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    color: #fff;
  }
  dt {
    font-size: 16px;
    margin-top: 10px;
  }
  a {
    color: #fff;
  }
}

// 广告
.ad{
  border-radius: 8px;
}

// 公告
.ann-wrap {
  height: 166px;
  .mod-bd {
    padding: 10px 15px;
  }
  li {
    background: url("../img/icon_dot.png") no-repeat left center;
    padding-left: 10px;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }
}

// 专题活动
.active-wrap {
  .mod-bd {
    padding: 15px 10px;
    height: 364px;
  }
  li {
    float: left;
    width: 50%;
    margin-bottom: 9px;
    text-align: center;
  }
}

// 工作便利
.work-wrap {
  .mod-bd {
    overflow-y: scroll;
  }
}

// 新闻
.news-wrap {
  .mod-bd {
    padding: 10px 20px;
    li {
      background: url("../img/icon_triangle.png") no-repeat left center;
      padding-left: 15px;
    }
  }
  time{
    float: right;
    color: $gray;
  }
}


// 页脚
.foot-wrap{
  font-size: 12px;
  text-align: center;
  border-top:2px solid $brand-primary;
  p{
    margin-top: 5px;
    margin-bottom: 10px;
  }
  span{
    margin-left: 5px;
    margin-right: 5px;
  }
}
